// app/components/top-bar.tsx
"use client"

import * as React from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { DatePickerWithPresets } from "./date-picker" // create or rename the date picker file
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/components/ui/select"

export function TopBar() {
  const [from, setFrom] = React.useState("")
  const [to, setTo] = React.useState("")
  const [timeRange, setTimeRange] = React.useState("") // or another approach

  return (
    <div className="flex flex-wrap items-center gap-4 p-4 bg-muted/50 rounded-md">
      {/* "Leaving from" */}
      <div className="flex flex-col">
        <label className="mb-1 text-sm font-medium">Leaving from</label>
        <Input
          placeholder="e.g. Yale"
          value={from}
          onChange={(e) => setFrom(e.target.value)}
        />
      </div>

      {/* "Going to" */}
      <div className="flex flex-col">
        <label className="mb-1 text-sm font-medium">Going to</label>
        <Input
          placeholder="e.g. Hartford (BDL)"
          value={to}
          onChange={(e) => setTo(e.target.value)}
        />
      </div>

      {/* "Date" (use date picker) */}
      <div className="flex flex-col">
        <label className="mb-1 text-sm font-medium">Date</label>
        <DatePickerWithPresets />
      </div>

      {/* "Time range" (could be a select or another input) */}
      <div className="flex flex-col">
        <label className="mb-1 text-sm font-medium">Time range</label>
        <Select onValueChange={(val) => setTimeRange(val)}>
          <SelectTrigger className="w-[200px]">
            <SelectValue placeholder="Pick a range" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="8-9am">8:00 AM - 9:00 AM</SelectItem>
            <SelectItem value="9-10am">9:00 AM - 10:00 AM</SelectItem>
            <SelectItem value="4-5pm">4:00 PM - 5:00 PM</SelectItem>
          </SelectContent>
        </Select>
      </div>

      {/* "Find a buddy" button */}
      <div className="ml-auto">
        <Button className="mt-5">Find a buddy</Button>
      </div>
    </div>
  )
}
